// Sass partial file

@import "variables";

// Global Variables
:root {
  font-size: var(--font-size); // Changing font size here will affect overall size
  font-family: var(--font-stack);
}





*, *:before, *:after {
  box-sizing: inherit;
}
html {
  box-sizing: border-box;
  font-size: 100%
}

html,
body {
  // IE 10-11 didn't like using min-height
  height: var(--full-size);
}
body {
  display: flex;
  flex-direction: column;
  background: var(--bg-color);
  color: var(--text-color);
  font: 1rem var(--font-stack);
  line-height: 1.5;
  // font-family: var(--font-stack); 
  // font-size: 100%; 
  // font-style: normal; 
  // font-variant: normal; 
  // font-weight: 700; 
  // For fixed nav bar:
  // min-height: 75rem; // Messes up Mapbox pages
  // padding-top: 4rem;
}

main {
  // height: 100%; // add height:100% for full page content
  margin-top: var(--navbar-size);
  padding: 0.25rem;
}

#title {
  text-align: center;
  padding: 10px;
  font-size: 24px;
  // letter-spacing: -0.01rem;
  color: var(--text-color);
}
h1 {
  // text-align: center;
  // padding: 0 0 1rem 1.5rem;
  font-size: 1.7rem; // 28px
  color: var(--text-color);
}
h2 {
  font-size: 1.4rem;
  color: var(--text-color);
}
h3 {
  color: var(--text-color);
  font-size: 1.1rem;
  // font-weight: 500; 
}

.content {
  // display: none;
  flex: 1 0 auto; /* Prevent Chrome, Opera, and Safari from letting these items shrink to smaller than their content's default minimum size. */
  padding: 2.5rem 0 0 1.5rem;
  // height: 100%; // add height:100% for full page content
}

.content-container {
  flex: 1 0 auto; /* Prevent Chrome, Opera, and Safari from letting these items shrink to smaller than their content's default minimum size. */
  padding: 1.25rem; // 2rem 1.5rem; / 20px;
  // padding: 5.5rem 1.5rem; // 20px
  // height: 100%; // add height:100% for full page content
  // display: none;

  // height: 100vh;
  // display: flex;
  // justify-content: center;
  // align-items: center;
  // padding: 0 10vw;
}


