.post a:link {
  color: var(--text-color-sec);
  background-color: transparent;
  text-decoration: none;
  outline: none;
}

.post a:visited {
  color: var(--text-color-alt);
  background-color: transparent;
}

.post a:hover {
  color: var(--text-color-link-hover);
  background-color: transparent;
}

