// Links
main a:link {
  color: var(--text-color-link);
  background-color: transparent;
  text-decoration: none;
  outline: none;
  font-weight: 600;
  // font-style: oblique;
}

main a:visited {
  color: var(--text-color-link-visited);
  background-color: transparent;
}



